import React from 'react';
import type { FC, ReactNode } from 'react';
import { Transform } from 'ink';
import terminalLink from 'terminal-link';
import { Text } from './Text.js';
import type { TextSkin } from './Text.js';

export interface LinkProps {
  skin?: TextSkin;
  url: string;
  transform?: (link: string) => string;
  children?: ReactNode;
}

export const Link: FC<LinkProps> = ({
  skin,
  children,
  url,
  transform = (v) => v,
}) => {
  if (children) {
    return (
      <Transform
        transform={(children) =>
          transform(terminalLink(children, url, { fallback: true }))
        }
      >
        <Text skin={skin}>{children}</Text>
      </Transform>
    );
  }

  return <Text skin="legacyBlue">{url}</Text>;
};
